body {
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  -webkit-focus-ring-color: rgba(255, 255, 255, 0);
  outline: none;
  -moz-user-select: none;
  -o-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  resize: none;
  padding: 0;
  margin: 0;
  font-family: RobotoCondensed, sans-serif;
  /* font-size: 14px; */
  font-size: 0.85em;
  color: #455a64;
  /* 
  background: -moz-linear-gradient(left, #1f1f27 0%, #20272f 50%, #1f1f27 100%);
  background: -webkit-linear-gradient(
    left,
    #1f1f27 0%,
    #20272f 50%,
    #1f1f27 100%
  ); */
  background: #14161a;
  background-image: -ms-linear-gradient(top, #14161a, #24262a);
  background: -webkit-linear-gradient(top, #14161a, #24262a);
  background: -moz-linear-gradient(top, #14161a, #24262a);
  /* border: solid thin #282d39; */
  border: solid thin rgba(0, 0, 0, 0);

  overflow: hidden;
  height: 100%;
  width: 100%;
  float: left;
  display: block;
}
/* hide scroll bar for chrome */
::-webkit-scrollbar {
  width: 2px;
  height: 2px;
  background-color: rgba(153, 153, 153, 0);
  display: none !important;
}

* {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

.App {
  position: absolute;
  width: 100%;
  height: 100%;
  float: left;
  left: 50%;
  text-align: center;
  background-color: transparent;
  background-image: url("./../Media/bg_panel.png");
  background-repeat: repeat-y;
  background-size: 100% 100%;
  background-position: center top;
  max-width: 450px;
  -ms-transform: translate(-50%, 0);
  -webkit-transform: translate(-50%, 0);
  transform: translate(-50%, 0);
  overflow: hidden;
}
.css-opacity {
  -khtml-opacity: 0;
  opacity: 0;
}
.css-fade {
  -khtml-opacity: 1;
  opacity: 1;
  -o-transition-delay: 0.5s;
     transition-delay: 0.5s;
  -webkit-transition-delay: 0.5s;
  -o-transition: opacity 0.6s ease-in-out;
  transition: opacity 0.6s ease-in-out;
  -moz-transition: opacity 0.6s ease-in-out;
  -webkit-transition: opacity 0.6s ease-in-out;
}

.title {
  width: 100%;
  padding: 7px 0 6px 0;
  font-size: 70%;
  letter-spacing: 4px;

  text-shadow: -1px -1px 0px #000;
  height: auto;
  background-image: url("./../Media/shadow.png");
  background-repeat: no-repeat;
  background-size: 100% 30px;
  background-position: center top;
  color: #897a1a;
  text-transform: uppercase;
}
.App-icon {
  background-image: url("./../Media/icon.png");
  height: 60px;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: auto 50%;
}

.box_info {
  position: relative;
  float: left;
  left: 0;
  width: 100%;
  height: auto;
  background-color: #101a20;
  /*border-top:solid 3px #282d39;
background-color:#102026;
background-image: url("line_bottom.png"), url("line_bottom.png");
background-repeat: no-repeat;
background-size: 80% 1px, 80% 3px;
background-position: center bottom, center top; 
margin-bottom:10px;

border-top:solid 3px #005f77;*/
  overflow: hidden;
  padding: 0;
  background-image: url("./../Media/top_blick.png"),
    url("./../Media/line_bottom.png");
  background-repeat: no-repeat, no-repeat;
  background-size: 100% 3px, 80% 1px;
  background-position: center top, center bottom;
}

.box-button {
  position: absolute;
  display: block;
  float: left;
  width: 100%;
  height: 50px;
  bottom: 1px;
  background-color: #000000;
  text-align: center;
  background-image: url("./../Media/line_bottom.png");
  background-position: center bottom;
  background-repeat: no-repeat;
  background-size: 80% 1px;
}

.display {
  position: absolute;
  display: block;
  width: 240px;
  height: 240px;
  overflow: hidden;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  float: left;
  border-radius: 50%;
  background-color: #000;
  margin: 30px auto;

  background-image: url("./../Media/light_effect.png");
  background-position: center center;
  background-size: auto 110%;
  background-repeat: no-repeat;

  /* border-top: solid 2px #24262a;
  border-left: solid 2px #24262a;
  border-bottom: solid 2px #303134;
  border-right: solid 2px #303134; */
}
.container {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  /* display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;  */

  background-image: none;
  background-position: center 50px;
  background-size: auto 40px;
  background-repeat: no-repeat;
}
.container.time {
  background-image: url("./../Media/sym_time.png");
}
.container.temperature {
  background-image: url("./../Media/temperature.png");
}
.display_value {
  position: absolute;
  text-align: left;
  font-size: 300%;
  padding: 5px;
  letter-spacing: -3px;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: #607d8b;
}
.display_value span {
  font-size: 95%;
  vertical-align: text-bottom;
  margin-left: 3px;
  /* width: 30px;
  background-image: url("./../Media/sym_temp.png");
  background-position: left top;
  background-size: auto 30px;
  background-repeat: no-repeat;
  color:rgba(0, 255, 0, 0); */
}
.display_name {
  position: absolute;
  text-align: center;
  font-size: 75%;
  padding: 5px;
  letter-spacing: 1px;
  top: 70%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: #607d8b;
}
.display_delta,
.display_delta_warmer {
  position: absolute;
  text-align: center;
  font-size: 80%;
  /*color: rgba(0, 204, 255, 0.75);*/
  color: #0faba5;
  letter-spacing: 0;
  top: 62%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.display_delta_warmer {
  color: rgba(255, 0, 33, 0.75);
}
@media only screen and (min-height: 768px) {
  .App-icon {
    height: 100px;
  }
  .title {
    padding: 24px 0 10px 0;
    font-size: 90%;
    color: #485d53;
    text-transform: none;
  }
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



