
.button-icon {
    display: inline-block;
    float: none;
    width: 100px;
    height: 100%;
    text-align: center;
    background-image: none;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: auto 75%;
    cursor: pointer;
    background-color: transparent;
    border: none;
    border-left: 2px solid #000;
  
    /*border-right: 2px solid #000; */
    -webkit-border-image: url(./../../Media/btn_stroke.png) 2;
    -o-border-image: url(./../../Media/btn_stroke.png) 2;
    border-image: url(./../../Media/btn_stroke.png) 2;
  
    margin: 0;
    outline: none;
  }
  .button-icon:disabled {
    pointer-events: none;
  }
  .button-icon:focus {
    outline: none;
  }
  .sym-start {
    border: none;
    background-image: url("./../../Media/sym_start.png");
  }
  .sym-start:active,
  .sym-start:hover,
  .sym-start.select {
    border: none;
    outline: none;
    background-image: url("./../../Media/sym_start_on.png");
  }
  
  .sym-list {
    background-image: url("./../../Media/sym_list.png");
  }
  .sym-list:hover,
  .sym-list.select {
    background-image: url("./../../Media/sym_list_on.png");
  }
  


